String.prototype.completeDate = String.prototype.completeDate || function (max_Date, min_Date) {
    let text = this;
    return new Promise((resolve, reject) => {
        try {
            if (!text) {
                reject({
                    code: 1,
                    message: "Không được truyền rỗng"
                });
                return;
            }
            text = text.trim();
            if (text.length > 10) {
                reject({
                    code: 2,
                    message: "Vui lòng nhập đúng định dạng ngày tháng"
                })
                return;
            }
            var _date = text.toDateObject();
            var year = new Date().getFullYear();
            var date = "";
            if (text.length === 4) {
                date = text[2] + text[3] + "/" + text[0] + text[1] + "/" + (year);
            } else if (text.length === 5) {
                date = text[3] + text[4] + "/" + text[0] + text[1] + "/" + (year);
            } else
                if (text.length === 6) {
                    var year = new Date().getFullYear();

                    if (parseInt(year.toString().substr(-2)) < parseInt(text[4] + text[5])) {
                        date = text[2] + text[3] + "/" + text[0] + text[1] + "/" +
                            (parseInt(year.toString().substr(0, 2)) - 1) + text[4] + text[5];
                    } else {
                        date = text[2] + text[3] + "/" + text[0] + text[1] + "/" +
                            year.toString().substr(0, 2) + text[4] + text[5];
                    }
                } else
                    if (text.length === 8) {
                        if (text.indexOf("/") != -1) {
                            return this.fixBOD2(text.replace(/\//g, ''), max_Date, min_Date);
                        } else {
                            date = text[2] +
                                text[3] +
                                "/" +
                                text[0] +
                                text[1] +
                                "/" +
                                text[4] +
                                text[5] +
                                text[6] +
                                text[7];
                        }
                    } else
                        if (text.length === 10) {
                            date = text[3] +
                                text[4] +
                                "/" +
                                text[0] +
                                text[1] +
                                "/" +
                                text[6] +
                                text[7] +
                                text[8] +
                                text[9];
                        }
                        else
                            if (text.length === 7) {
                                date = "";
                            }

                            else {
                                var temp = text.split("/");
                                date = temp[1][0] +
                                    temp[1][1] +
                                    "/" +
                                    temp[0][0] +
                                    temp[0][1] +
                                    "/" +
                                    temp[2][0] +
                                    temp[2][1] +
                                    temp[2][2] +
                                    temp[2][3];
                            }

            var _date = date.toDateObject();
            if (!_date.isValidDate()) {
                reject({
                    code: 2,
                    message: "Vui lòng nhập đúng định dạng ngày tháng"
                })
            } else {
                if (min_Date != undefined && _date < min_Date) {
                    reject({
                        code: 3,
                        message: "Vui lòng nhập ngày lớn hơn " + min_Date.format("dd/MM/yyyy")
                    })
                    return;
                }
                if (max_Date != undefined && _date > max_Date) {
                    reject({
                        code: 4,
                        message: "Vui lòng nhập ngày nhỏ hơn " + max_Date.format("dd/MM/yyyy")
                    })
                    return;
                }
                resolve(_date);
            }
        } catch (e) {
            console.log(e);
            reject({
                code: 2,
                message: "Vui lòng nhập đúng định dạng ngày tháng"
            })
            return;
        }

    })
}

